<template>
  <div class="mt-6">
    <ul class="icon-container flex flex-wrap">
      <li
        class="bg-gray-50 dark:bg-gray-600 p-2 rounded shadow mr-2 xs:mb-2 mb-1 md:mb-2"
        v-for="(icon, i) in iconObj"
        :key="i"
      >
        <img :src="icon.imgSrc" :alt="icon.alt" />
        <p>{{ icon.text }}</p>
      </li>
      <slot name="jquery"></slot>
      <slot name="tailwind"></slot>
    </ul>
  </div>
</template>

<script>
import html from '../../assets/icon/icons8-html-5.svg'
import css from '../../assets/icon/icons8-css3.svg'
import scss from '../../assets/icon/file-type-scss.svg'
import js from '../../assets/icon/icons8-javascript.svg'
import vue from '../../assets/icon/icons8-vue-js.svg'
import php from '../../assets/icon/file-type-php.svg'
import laravel from '../../assets/icon/laravel.svg'
import sql from '../../assets/icon/icons8-mysql-logo.svg'

export default {
  props: {
    iconLength: {
      type: Number,
      default: null,
    },
  },
  computed: {
    iconObj() {
      return this.iconLength ? this.icons.slice(0, this.iconLength) : this.icons
    },
  },
  data() {
    return {
      icons: [
        {
          imgSrc: html,
          text: 'HTML5',
          alt: 'html5',
        },
        {
          imgSrc: css,
          text: 'CSS3',
          alt: 'css',
        },
        {
          imgSrc: scss,
          text: 'SCSS',
          alt: 'scss',
        },
        {
          imgSrc: js,
          text: 'JavaScript',
          alt: 'javascript',
        },
        {
          imgSrc: vue,
          text: 'VueJs',
          alt: 'vue',
        },
        {
          imgSrc: php,
          text: 'PHP',
          alt: 'php',
        },
        {
          imgSrc: laravel,
          text: 'Laravel',
          alt: 'laravel',
        },
        {
          imgSrc: sql,
          text: 'MySQL',
          alt: 'mysql',
        },
      ],
    }
  },
}
</script>

<style lang="scss" scoped>
.icon-container {
  li {
    width: 37px;
    height: 37px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    p {
      font-size: 8px;
    }
    img {
      width: 100%;
    }
  }
}
</style>
